<template>
  <div class="other">
    <el-input type="textarea" class="other_input"  v-model="selfMsg"></el-input>
    <el-button class="other_update" @click="updateInfo(selfMsg)">立即更新</el-button>
  </div>
</template>

<script>
import InfoJson from '../../another'
import api from '../../api/index'
export default {
  data(){
    return {
      selfMsg:InfoJson['self'+InfoJson.num],
    }
  },
  methods:{
    updateInfo(aboutInfo){
      api.editAbout({aboutInfo}).then(res =>{
        if(res.data.code === 200){
          this.$notify({
            title:'成功',
            message:res.data.message,
            type:'success'
          })
        }else{
          this.$notify({
            title:'失败',
            message:res.data.message,
            type:'error'
          })
        }
      })
    }
  },
}
</script>

<style lang="scss" scoped>
  .other{
    display: flex;
    flex-direction: column;
    align-items: center;
    &_input{
      padding: 30px 0;
      width: 600px;
    }
  }
</style>




